import React from "react";
import { Link } from "react-router-dom";

import "./typeNav.css";

const TypeNav = ({ style, DATA }) => {
  return (
    <div className="TypeNav" style={{ ...style }}>
      <Link
        style={{ color: "#000" }}
        className="typeNavText typeNavBlock"
        to={{
          pathname: "/map",
          state: {
            DATA
          }
        }}
      >
        <p>КАРТА</p>
      </Link>
      <Link
        style={{ color: "#000" }}
        className="typeNavText typeNavBlock"
        to={{ pathname: "/home", state: "Some state" }}
      >
        <p>СПИСОК </p>
      </Link>
    </div>
  );
};

export default TypeNav;
