import React, { useState, useRef } from "react";
import GooggleMapReact from "google-map-react";
import useSupercluster from "use-supercluster";
import { Link } from "react-router-dom";

import { DATA } from "../../DATA";

import "./map.css";

import TypeNav from "../../components/typeNav/TypeNav";
import CompanyNav from "../../components/companyNav/CompanyNav";
import SlideSideMenu from "../../components/slideSideMenu/SlideSideMenu";

const Marker = ({ children }) => children;

const MapComponent = props => {
  const [markers, setMarkers] = useState(DATA);

  const mapRef = useRef();
  const [zoom, setZoom] = useState(10);
  const [bounds, setBounds] = useState(null);

  const points = markers.map((el, i) => ({
    type: "Feature",
    item: el,
    properties: {
      cluster: false,
      crimeId: i,
      category: el.company_type
    },
    geometry: {
      type: "Point",
      coordinates: [parseFloat(el.location.lon), parseFloat(el.location.lat)]
    }
  }));

  const { clusters } = useSupercluster({
    points,
    bounds,
    zoom,
    options: {
      radius: 100,
      maxZoom: 20
    }
  });

  const clickedType = type => {
    if (type) {
      const filteredData = DATA.filter(el => el.company_type === type);
      setMarkers(filteredData);
    } else {
      setMarkers(DATA);
    }
  };

  const [showSlideSideMenu, setShowSlideSideMenu] = useState(false);
  const [isShowMenu, setIsShowMenu] = useState(false);

  const hideSideMenu = () => {
    setShowSlideSideMenu(false);
    setTimeout(() => {
      document.body.style.overflow = "visible";
    }, 400);
    setIsShowMenu(false);
  };

  const showSideMenu = () => {
    setShowSlideSideMenu(true);
    document.body.style.overflow = "hidden";
    setIsShowMenu(true);
  };

  window.onresize = function(e) {
    hideSideMenu();
  };

  return (
    <div
      onClick={e => {
        if (e.target.className !== "SlideSideMenu" && showSlideSideMenu) {
          hideSideMenu();
        }
      }}
    >
      <div
        className="headerMap"
        style={
          isShowMenu
            ? {
                animation: "toLeft 0.4s ease",
                position: "relative",
                right: "200px"
              }
            : {
                animation: "toRight 0.2s ease",
                position: "relative",
                right: "0px"
              }
        }
      >
        <span
          className="burger"
          onClick={() => {
            if (!showSlideSideMenu) {
              showSideMenu();
            }
          }}
        >
          <span></span>
        </span>
      </div>
      <div className="navContainerMap">
        <CompanyNav
          style={{ zIndex: 1 }}
          currentPage="/map"
          clickedType={clickedType}
        />
        <TypeNav style={{ zIndex: 1 }} />
      </div>
      <div className="mapContainer">
        <GooggleMapReact
          style={{ height: "100%", width: "100%" }}
          bootstrapURLKeys={{ key: "AIzaSyDX0WzPwqT3ZDzTYJAeO3VGPS6YQgYFudM" }}
          defaultCenter={{ lat: 47.49855629475769, lng: -122.14184416996333 }}
          defaultZoom={10}
          yesIWantToUseGoogleMapApiInternals
          onGoogleApiLoaded={({ map }) => {
            mapRef.current = map;
          }}
          onChange={({ zoom, bounds }) => {
            setZoom(zoom);
            setBounds([
              bounds.nw.lng,
              bounds.se.lat,
              bounds.se.lng,
              bounds.nw.lat
            ]);
          }}
        >
          {clusters.map(cluster => {
            const [longitude, latitude] = cluster.geometry.coordinates;
            const {
              cluster: isCluster,
              point_count: pointCount
            } = cluster.properties;

            //ЗАМЕНА НА ЦИФРЫ
            if (isCluster) {
              return (
                <Marker
                  key={cluster.properties.cluster_id}
                  lat={latitude}
                  lng={longitude}
                >
                  <div className="clusterMarker">
                    <p> {pointCount}</p>
                  </div>
                </Marker>
              );
            }
            //БЕЗ ЗАМЕНЫ НА ЦИФРЫ
            return (
              <Marker
                key={cluster.properties.crimeId}
                lat={latitude}
                lng={longitude}
              >
                <Link
                  to={{
                    pathname: "/company",
                    state: cluster.item
                  }}
                  style={{ background: "red", zIndex: 100 }}
                >
                  <div className="mapMarkerWrap">
                    <div className="mapMarker">
                      {cluster.item.url && (
                        <img alt="img" src={cluster.item.url}></img>
                      )}
                      {!cluster.item.url && (
                        <p className="markerText">
                          Начало трансляции сегодня в 19:00
                        </p>
                      )}
                    </div>
                    <p className="mapMarkerName">{cluster.item.company_name}</p>
                    <div className="arrow"></div>
                  </div>
                </Link>
              </Marker>
            );
          })}
        </GooggleMapReact>
      </div>
      <SlideSideMenu isShowMenu={isShowMenu} />
    </div>
  );
};

export default MapComponent;
