  const clickedType = type => {
    if (type) {
      const filteredData = DATA.filter(
        el => el.company_type.toUpperCase() === type.toUpperCase()
      );
      setCompanyData(filteredData);
    } else {
      setCompanyData(DATA);
    }
  };

  const [showSlideSideMenu, setShowSlideSideMenu] = useState(false);
  const [isShowMenu, setIsShowMenu] = useState(false);

  const hideSideMenu = () => {
    setShowSlideSideMenu(false);
    setTimeout(() => {
      document.body.style.overflow = "visible";
    }, 400);
    setIsShowMenu(false);
  };

  const showSideMenu = () => {
    setShowSlideSideMenu(true);
    document.body.style.overflow = "hidden";
    setIsShowMenu(true);
  };

  window.onresize = function(e) {
    hideSideMenu();
  };

  return (
    <div
      onClick={e => {
        if (e.target.className !== "SlideSideMenu" && showSlideSideMenu) {
          hideSideMenu();
        }
      }}
    >
      <div
        style={
          isShowMenu
            ? {
                animation: "toLeft 0.4s ease",
                position: "relative",
                right: "200px"
              }
            : {
                animation: "toRight 0.2s ease",
                position: "relative",
                right: "0px"
              }
        }
      >
        <div className="headerContainer">
          <div className="header">
            <Link to="/home">
              <p className="party_live">
                PARTY<span className="live">.LIVE</span>
              </p>
              <span
                className="burger"
                onClick={() => {
                  if (!showSlideSideMenu) {
                    showSideMenu();
                  }
                }}
              >
                <span></span>
              </span>
            </Link>
          </div>
        </div>
        <div className="homeContentWrap">
          <div className="navContainer">
            <CompanyNav
              currentPage="/home"
              clickedType={type => {
                clickedType(type);
              }}
            />
            <TypeNav DATA={DATA} />
          </div>
          <div className="homeContent">
            {/* {quickStreemTimeSort(quickWorkTimeSort(companyData)).map(
              (el, i) => (
                <SmallCompanyBlock item={el} key={i} />
              )
            )} */}
            {companyData.map((el, i) => (
              <SmallCompanyBlock item={el} key={i} />
            ))}
          </div>
        </div>
      </div>
      <SlideSideMenu isShowMenu={isShowMenu} />
    </div>
  );
