import React from "react";
import GooggleMapReact from "google-map-react";

import "./popup.css";

const Marker = ({ children }) => children;

const Popup = ({ closePopup }) => {
  return (
    <div className="popup">
      <div className="popup_inner">
        <GooggleMapReact
          style={{ height: "100%", width: "100%" }}
          // bootstrapURLKeys={{key: MY_KEY}}
          defaultCenter={{ lat: 47.49855629475769, lng: -122.14184416996333 }}
          defaultZoom={10}
        >
          <Marker lat={47.49855629475769} lng={-122.14184416996333}>
            <button className="mapMarker">
              <img
                alt="img"
                src="https://static.relax.by/images/common/catalog/catalog_promo_block_images/6d0223deda15cc3d87448ac670decd73.png"
              ></img>
            </button>
          </Marker>
        </GooggleMapReact>
        <div className="closeBtn" onClick={closePopup}>
          &#215;
        </div>
      </div>
    </div>
  );
};
export default Popup;
