import React from "react";
import { Link } from "react-router-dom";
import { DATA } from "../../DATA";

import "./companyNav.css";

const CompanyNav = ({ style, clickedType, currentPage }) => {
  let tmpArray = [];
  const itemCheck = item => {
    if (tmpArray.indexOf(item.company_type) === -1) {
      tmpArray.push(item.company_type);
      return true;
    }
    return false;
  };

  const uniqueCompanyType = DATA.filter(item => itemCheck(item));

  return (
    <div className="сompanyNav" style={{ ...style }}>
      <div
        className="companyNavBlock"
        onClick={() => {
          clickedType();
        }}
      >
        <Link to={currentPage} className="companyNavLink">
          <p className="allText">ВСЕ</p>
        </Link>
      </div>

      {uniqueCompanyType.length &&
        uniqueCompanyType.map((el, i) => {
          return (
            <div
              key={i}
              className="companyNavBlock"
              onClick={() => {
                clickedType(el.company_type);
              }}
            >
              <Link
                to={currentPage + "/" + el.company_type_en}
                className="companyNavLink"
              >
                <img
                  alt="Icon"
                  className="сompanyNavImg"
                  src={`../img/${el.company_type_en}.png`}
                  width="30"
                  height="30"
                />
                <p className="сompanyNavText">{el.company_type}</p>
              </Link>
            </div>
          );
        })}
    </div>
  );
};

export default CompanyNav;
