import React, { useState } from "react";
import { Link } from "react-router-dom";
import GooggleMapReact from "google-map-react";

import Popup from "../../components/mapPopup/Popup";
import SlideSideMenu from "../../components/slideSideMenu/SlideSideMenu";

import "./company.css";

const Marker = ({ children }) => children;

const Company = props => {
  const [showPopup, setShowPopap] = useState(false);

  const togglePopup = () => {
    showPopup ? setShowPopap(false) : setShowPopap(true);
  };

  const {
    company_name,
    company_type,
    company_address,
    party_type,
    work_time,
    streem_time,
    company_desc,
    location
  } = props.location.state;

  const [showSlideSideMenu, setShowSlideSideMenu] = useState(false);
  const [isShowMenu, setIsShowMenu] = useState(false);

  const hideSideMenu = () => {
    setShowSlideSideMenu(false);
    setTimeout(() => {
      document.body.style.overflow = "visible";
    }, 400);
    setIsShowMenu(false);
  };

  const showSideMenu = () => {
    setShowSlideSideMenu(true);
    document.body.style.overflow = "hidden";
    setIsShowMenu(true);
  };

  window.onresize = function(e) {
    hideSideMenu();
  };

  return (
    <div
      onClick={e => {
        if (e.target.className !== "SlideSideMenu" && showSlideSideMenu) {
          hideSideMenu();
        }
      }}
    >
      <div
        className="Company"
        style={
          isShowMenu
            ? {
                animation: "toLeft 0.4s ease",
                position: "relative",
                right: "200px"
              }
            : {
                animation: "toRight 0.2s ease",
                position: "relative",
                right: "0px"
              }
        }
        onClick={e => {
          if (e.target.className !== "SlideSideMenu" && showSlideSideMenu) {
            hideSideMenu();
          }
        }}
      >
        <div className="headerCompany">
          <Link
            to={{ pathname: "/home", state: "Some state" }}
            className="toHome"
          >
            &#60; На главную
          </Link>
          <span
            className="burger"
            onClick={() => {
              if (!showSlideSideMenu) {
                showSideMenu();
              }
            }}
          >
            <span></span>
          </span>
        </div>
        <div className="flex">
          <div className="videoBlock">
            <iframe
              title="key"
              className="video"
              src="https://www.youtube.com/embed/MVqeTTNftAk"
              frameBorder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            ></iframe>
            <div>
              <div className="row">
                <img src={"./img/eye.png"} className="eye" />
                <p className="leftText">75862</p>
                <span className="circle"> </span>
                <p className="leftText">387</p>
              </div>
            </div>
            <p className="videoDescription">
              <span>{company_name}</span> - {company_desc}
            </p>
          </div>
          <div className="desc">
            <h3>{company_name}</h3>
            <p className="typeOfPati">{party_type}</p>
            <p className="dayOfWeek">СРЕДА</p>
            <div className="timeBlock">
              <img height="16" width="16" src="./img/clock.png" />
              <p className="rightTimeBlock">
                <span>Время работы:</span> <span>{work_time.join(" - ")}</span>
              </p>
            </div>
            <div className="timeBlock">
              <img height="16" width="16" src="./img/camera.png" />
              <p className="rightTimeBlock">
                <span> Стрим:</span> <span>{streem_time.join(" - ")}</span>
              </p>
            </div>
            <div className="timeBlock">
              <img height="16" width="16" src="./img/location.png" />
              <p className="rightTimeBlock">
                <span>Адрес: </span>
                <span>{company_address}</span>
              </p>
            </div>
            <div
              className="smallMap"
              onClick={() => {
                togglePopup();
              }}
            >
              <GooggleMapReact
                // bootstrapURLKeys={{key: MY_KEY}}
                defaultCenter={{
                  lat: 47.49855629475769,
                  lng: -122.14184416996333
                }}
                defaultZoom={10}
              >
                <Marker lat={location.lat} lng={location.lon}>
                  <button className="mapMarker">
                    <img
                      alt="img"
                      src="https://static.relax.by/images/common/catalog/catalog_promo_block_images/6d0223deda15cc3d87448ac670decd73.png"
                    ></img>
                  </button>
                </Marker>
              </GooggleMapReact>
            </div>
          </div>
          <div className="type">{company_type}</div>
        </div>
        {showPopup && <Popup closePopup={togglePopup} />}
      </div>
      <SlideSideMenu isShowMenu={isShowMenu} />
    </div>
  );
};

export default Company;
